import React, {Component} from 'react';
import {Card, CardContent, Tooltip, IconButton} from '@material-ui/core';
import Zoom from '@material-ui/core/Zoom';
import GithubIcon , {DemoIcon} from './GithubIcon';

export default class Project extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: this.props.content.title,
      image: this.props.content.image,
      content: this.props.content.content,
      github: this.props.content.github,
      demo: this.props.content.demo
    }

    this.onClickGithub = this.onClickGithub.bind(this);
    this.onClickDemo = this.onClickDemo.bind(this);
  }

  onClickGithub() {
    window.open(this.state.github, '_blank');
  }

  onClickDemo() {
    window.open(this.state.demo, '_blank');
  }

  render() {
    let demo;
    if (this.state.demo === undefined) {
      demo = <div></div>
    } else {
      demo = 
        <Tooltip title="Demo" placement="top" TransitionComponent={Zoom} >
          <IconButton onClick={this.onClickDemo} className="githubIcon">
            <DemoIcon />
          </IconButton>
        </Tooltip>
    }
    return (
      <Card className="project-card">
        <CardContent className="project-card-content">
          <div className="project-container__image">
            <img src={this.state.image} alt="img"/>
          </div>
          <div className="project-content">
            <h2 className="project-content__title">{this.state.title}</h2>
            <p className="project-content__text">{this.state.content}</p>
            <div className="project-links" >
              {demo}
              <Tooltip title="GitHub" placement="top" TransitionComponent={Zoom}>
                <IconButton onClick={this.onClickGithub} className="githubIcon" >
                  <GithubIcon />
                </IconButton>
              </Tooltip>
            </div>
          </div>
        </CardContent>
      </Card>
    )
  }
}