import React, {Component} from 'react';
import Project from '../components/Project';
import {content, websites} from '../content';
import { Grid, Card, ButtonBase, Icon, IconButton } from '@material-ui/core';

export default class Projects extends Component {
  render() {
    return (
      <div className="projects-page">
        <div className="projects-header">
          <h1 className="projects-header__text">Projects</h1>
        </div>
        <div className="projects-content">
          {content.map((project, key) => (
            <Project content={project} key={key}/>
          ))}
        </div>
        <div className="projects-header">
          <h1 className="projects-header__text">Other websites I've worked on</h1>
        </div>
        <div className="projects-content">
          <Grid container className="projects-sites">
            {websites.map((site, key) => (
              <Grid item>
                <Card className="projects-site-block">
                  <ButtonBase onClick={() => window.open(site.url, '_blank')} centerRipple={true}>
                    <div className="projects-site-img--container">
                      <img src={site.image} alt={site.title} className="projects-site-img" />
                    </div>
                    <div className="projects-site-content">
                      <div className="projects-site-title">
                        <h3 className="project-content__title">{site.title}</h3>
                      </div>
                      <p className="project-content__text">{site.desc}</p>
                    </div>
                  </ButtonBase>
                </Card>
              </Grid>
            ))}
          </Grid>
        </div>
      </div>
    )
  }
}